#tweet-quote {
  font-size: 2rem;
}

#quote-box {
  transition: background-color 1s;
  min-height: 100vh;
  height: 100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}

.display-4 {
  padding: 20px;
  width: fit-content;
  color: white;
  background-color: rgba(0, 0, 0, 0.7) !important;
  border-radius: 30px;
  margin: auto;
  margin-top: 30px;
  border: 4px solid white;
}

.card {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.7) !important;
}
